import React, { Component } from 'react'
import PropTypes from 'prop-types'

import { Upload, Icon, Button, Progress, Checkbox, Modal, Spin, Radio, message } from 'antd'
import 'antd/dist/antd.css';
import request from 'superagent'
import SparkMD5 from 'spark-md5'

const confirm = Modal.confirm
const Dragger = Upload.Dragger

const url = `//10.60.102.252:20075`
const BlockSIZE = 1024 * 1024;
// const url = `//192.168.80.230:2000`;

class FolderUpload extends Component {
    constructor(props) {
        super(props)
        this.state = {
            preUploading: false,   //
            chunksSize: 0,   // 
            currentChunks: 0,  // 
            uploadPercent: -1,  // 
            preUploadPercent: -1, //   
            uploadRequest: false, // 
            uploaded: false, // 
            uploading: false, // 
        }
    }
    showConfirm = () => {
        const _this = this
        if (window.confirm("?")) {
            _this.preUpload()
        }

        // confirm({
        //     title: '?',
        //     content: '',
        //     onOk() {
        //         _this.preUpload()
        //     },
        //     onCancel() { },
        // })
    }


    preUpload = () => {
        const _this = this;
        // requestUrl,
        request
            .post(url)
            .send(JSON.stringify(_this.state.uploadParams))
            .set('Accept', 'application/json')
            .set('Content-Type', 'application/x-www-form-urlencoded')
            //.withCredentials()
            .then(res => {
                console.log(res);
                if (res.ok) {
                    let currentChunks = this.state.currentChunks
                    --currentChunks
                    // 
                    let uploadPercent = Number(((this.state.chunksSize - currentChunks) / this.state.chunksSize * 100).toFixed(2))
                    this.setState({
                        currentChunks,  // chunks
                        uploadPercent,
                        uploading: true
                    })
                    if (currentChunks === 0) {
                        // api
                        //this.checkUploadStatus(this.state.fileMd5)
                    }
                }

                var json_obj = JSON.parse(res.text.toString());
                console.log(json_obj);
                /*
                let uploadList = res.data.Chunks.filter((value) => {
                    return value.status === 'Pending'
                })
                */

                // 
                let currentChunks = json_obj.Total - json_obj.Uploaded

                // 
                let uploadPercent = Number(((this.state.chunksSize - currentChunks) / this.state.chunksSize * 100).toFixed(2))
                // 
                if (uploadPercent === 100) {
                    message.success('')
                    this.setState({
                        uploaded: true,    // 
                        uploading: false
                    })
                } else {
                    this.setState({
                        uploaded: false,
                        uploading: true
                    })
                }

                this.setState({
                    uploadRequest: false,    // 
                    currentChunks,
                    uploadPercent
                })
                console.log('preupload');
                //
                //this.handlePartUpload(uploadList)
                this.UploadChunk(json_obj.No);
            })
            .catch(error => {
                console.log(error)
                return error
            })
    }

    UploadChunk = (ChunkNo) => {
        const _this = this;
        let formData = new FormData(),
            blob = new Blob([_this.state.arrayBufferData[ChunkNo - 1].currentBuffer], { type: 'application/octet-stream' })

        var info = JSON.stringify({
            file: _this.state.uploadParams.file,
        });

        var name = ChunkNo + '?' + _this.state.arrayBufferData[ChunkNo - 1].len;
        
        formData.append('file_info', info)
        formData.append('file_data', blob, name)

        request
            .post(url)
            .send(formData)
            //.withCredentials()
            .then(res => {
                if (res.ok) {
                    let currentChunks = this.state.currentChunks
                    --currentChunks
                    // 
                    let uploadPercent = Number(((this.state.chunksSize - currentChunks) / this.state.chunksSize * 100).toFixed(2))
                    this.setState({
                        currentChunks,  // chunks
                        uploadPercent,
                        uploading: true
                    })

                    // 
                    if (uploadPercent === 100) {
                        message.success('')
                        this.setState({
                            uploaded: true,    // 
                            uploading: false
                        })
                    } else {
                        this.setState({
                            uploaded: false,
                            uploading: true
                        })
                    }

                    var json_obj = JSON.parse(res.text.toString());
                    console.log(json_obj);
                    this.UploadChunk(json_obj.No);
                }
            })
            .catch(error => {
                console.log(error)
                return error
            })
    }//UploadChunk
    render() {
        const { preUploading, uploadPercent, preUploadPercent, uploadRequest, uploaded, uploading } = this.state
        const _this = this
        const uploadProp = {
            method: 'post',
            action: url,
            multiple: true,
            directory: true,
            onRemove: (file) => {
                this.setState(({ fileList }) => {
                    const index = fileList.indexOf(file)
                    const newFileList = fileList.slice()
                    newFileList.splice(index, 1)
                    return {
                        fileList: newFileList,
                    }
                })
            },
            beforeUpload: (file) => {
                // 
                this.setState({
                    uploaded: false,   // 
                    uploading: false,  // 
                    uploadRequest: false   // 
                })
                // 
                let blobSlice = File.prototype.slice || File.prototype.mozSlice || File.prototype.webkitSlice,
                    chunkSize = BlockSIZE,                             // 
                    chunks = Math.ceil(file.size / chunkSize),
                    currentChunk = 0, // chunk
                    spark = new SparkMD5.ArrayBuffer(),
                    // arrayBuffermd5md5
                    chunkFileReader = new FileReader(),  // chunkMd5
                    totalFileReader = new FileReader()   // fileMd5

                //fileReader = new FileReader()
                let params = { type: 'upload_req', file: {}, chunks: [] },   // md5
                    arrayBufferData = []              // chunkarrayBuffer,
                params.file.fileName = file.name
                params.file.fileSize = file.size

                totalFileReader.readAsArrayBuffer(file)
                totalFileReader.onload = function (e) {
                    // totalFilemd5
                    spark.append(e.target.result)
                    params.file.fileMd5 = spark.end() // fileMd5
                    //console.log('md5:' + params.file.fileMd5);
                    console.log(params);
                    //console.log('chunknum:' + chunks);
                }

                chunkFileReader.onload = function (e) {

                    // md5
                    //spark.append(e.target.result)
                    // 
                    let obj = {
                        chunk: currentChunk + 1,
                        start: currentChunk * chunkSize, // 
                        end: ((currentChunk * chunkSize + chunkSize) >= file.size) ? file.size : currentChunk * chunkSize + chunkSize, // 
                        //chunkMd5: spark.end(),
                        chunks
                    }
                    // onload,currentChunk
                    currentChunk++;
                    params.chunks.push(obj)
                    //console.log('currentChunk:' + currentChunk);
                    // arrayBufferpartUpload
                    let tmp = {
                        chunk: obj.chunk,
                        len: obj.end - obj.start,
                        currentBuffer: e.target.result
                    }
                    arrayBufferData.push(tmp)

                    if (currentChunk < chunks) {
                        // 
                        loadNext()

                        // 
                        _this.setState({
                            preUploading: true,
                            preUploadPercent: Number((currentChunk / chunks * 100).toFixed(2))
                        })
                    } else {
                        //chunks
                        params.file.fileChunks = params.chunks.length
                        params.file.filePath = _this.props.myfolderid;
                        params.file.RelativePath = file.webkitRelativePath;
                        params.file.username = _this.props.userID;
                        // arrayBuffer
                        _this.setState({
                            preUploading: false,
                            uploadParams: params,
                            arrayBufferData,
                            chunksSize: chunks,
                            preUploadPercent: 100
                        })
                    }
                }

                chunkFileReader.onerror = function () {
                    console.warn('oops, something went wrong.');
                };

                function loadNext() {
                    var start = currentChunk * chunkSize,
                        end = ((start + chunkSize) >= file.size) ? file.size : start + chunkSize;
                    chunkFileReader.readAsArrayBuffer(blobSlice.call(file, start, end));
                }

                loadNext()

                return false
            },
        }


        return (
            <div className="content-inner">
                <Spin tip={
                    <div >
                        <h3 style={{ margin: '10px auto', color: '#1890ff' }}>...</h3>
                        <Progress width={80} percent={preUploadPercent} type="circle" status="active" />
                    </div>
                }
                    spinning={preUploading}
                    style={{ height: 350 }}>
                    <div style={{ marginTop: 16, height: 250 }}>
                        <Dragger {...uploadProp}>
                            <p className="ant-upload-drag-icon">
                                <Icon type="inbox" />
                            </p>
                            <p className="ant-upload-text"></p>
                            <p className="ant-upload-hint">Support for a single or bulk upload. Strictly prohibit from uploading company data or other band files</p>
                        </Dragger>
                        {uploadPercent >= 0 && !!uploading && <div style={{ marginTop: 20, width: '95%' }}>
                            <Progress percent={uploadPercent} status="active" />
                            <h4></h4>
                        </div>}
                        {!!uploadRequest && <h4 style={{ color: '#1890ff' }}>...</h4>}
                        {!!uploaded && <h4 style={{ color: '#52c41a' }}></h4>}
                        <Button type="primary" onClick={this.showConfirm} disabled={!!(this.state.preUploadPercent < 100)}>
                            <Icon type="upload" />
             </Button>
                    </div>
                </Spin>
            </div>
        )
    }
}

FolderUpload.propTypes = {
    //...
}

export default FolderUpload